/* Projects */
@media(min-width: 960px) {
    .project-container {
        display: flex;
        flex-direction: row;
        justify-content: space-evenly;
        align-items: center;
    }

    .project-container-reverse {
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-evenly;
        align-items: center;
    }
}

@media(max-width: 960px) {
    .project-container {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

    }

    .project-container-reverse {
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;

    }
}

.project-description {
    margin: 100px 50px;
}