.centered-grid {
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    width: 100%;
    align-items: center;
}



.grid {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    flex-wrap: wrap;
    width: 100%;
    align-items: flex-start;
}

.grid-item-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    /* background-color: orange; */
    margin: 0px 20px;
    /* min-width: fit-content; */
}

.narrow-grid-item {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: center;
    margin: 0px 5px;
    max-width: 500px;
    /* min-width: fit-content; */
    /* background-color: purple; */
}


